 import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import { normalizePassiveListenerOptions, Platform } from '@angular/cdk/platform';
 import { DOCUMENT } from '@angular/common';
 import {
   AfterViewInit,
   ChangeDetectionStrategy,
   ChangeDetectorRef,
   Component,
   ElementRef,
   EventEmitter,
   Inject,
   Input,
   NgZone,
   OnChanges,
   OnDestroy,
   Output,
   Renderer2,
   SimpleChanges,
   ViewChild,
   ViewEncapsulation
 } from '@angular/core';
 import { fromEvent, Subject } from 'rxjs';
 import { takeUntil, throttleTime } from 'rxjs/operators';
import { NgStyleInterface, TDSConfigKey, TDSConfigService, WithConfig } from 'tds-ui/core/config';
import { TDSScrollService } from 'tds-ui/core/services';
import { InputBoolean, InputNumber, TDSSafeAny } from 'tds-ui/shared/utility';
import { TDSAnchorLinkComponent } from './anchor-link.component';
 
 import { getOffsetTop } from './util';
 
 interface Section {
   comp: TDSAnchorLinkComponent;
   top: number;
 }
 
 const TDS_CONFIG_MODULE_NAME: TDSConfigKey = 'anchor';
 const sharpMatcherRegx = /#([^#]+)$/;
 
 const passiveEventListenerOptions = normalizePassiveListenerOptions({ passive: true });
 
 @Component({
   selector: 'tds-anchor',
   exportAs: 'tdsAnchor',
   preserveWhitespaces: false,
   template: `
    <tds-affix *ngIf="tdsAffix; else content" [offsetTop]="tdsOffsetTop!" [tdsTarget]="container">
      <ng-template [ngTemplateOutlet]="content"></ng-template>
    </tds-affix>
    <ng-template #content>
      <div class="tds-anchor-wrapper " [ngStyle]="wrapperStyle">
        <div class="tds-anchor flex flex-col border-l border-neutral-2-100 dark:border-d-neutral-2-100" [ngClass]="{ fixed: !tdsAffix && !tdsShowInkInFixed }">
          <div class="tds-anchor-ink">
            <div class="tds-anchor-ink-ball" #ink></div>
          </div>
          <ng-content></ng-content>
        </div>
      </div>
    </ng-template>
   
   `,
   encapsulation: ViewEncapsulation.None,
   changeDetection: ChangeDetectionStrategy.OnPush
 })
 export class TDSAnchorComponent implements OnDestroy, AfterViewInit, OnChanges {
   readonly _tdsModuleName: TDSConfigKey = TDS_CONFIG_MODULE_NAME;
   static ngAcceptInputType_tdsAffix: BooleanInput;
   static ngAcceptInputType_tdsShowInkInFixed: BooleanInput;
   static ngAcceptInputType_tdsBounds: NumberInput;
   static ngAcceptInputType_tdsOffsetTop: NumberInput;
 
   @ViewChild('ink', { static: false }) private ink!: ElementRef;
 
   @Input() @InputBoolean() tdsAffix = true;
 
   @Input()
   @WithConfig()
   @InputBoolean()
   tdsShowInkInFixed: boolean = false;
 
   @Input()
   @WithConfig()
   @InputNumber()
   tdsBounds: number = 5;
 
   @Input()
   @InputNumber(undefined)
   @WithConfig<number>()
   tdsOffsetTop?: number = undefined;
 
   @Input()
   @InputNumber(undefined)
   @WithConfig<number>()
   tdsTargetOffset?: number = undefined;
 
   @Input() tdsContainer?: string | HTMLElement;
   @Input() tdsCurrentAnchor?: string;
 
   @Output() readonly tdsClick = new EventEmitter<string>();
   @Output() readonly tdsChange = new EventEmitter<string>();
   @Output() readonly tdsScroll = new EventEmitter<TDSAnchorLinkComponent>();
 
   visible = false;
   wrapperStyle: NgStyleInterface = { 'max-height': '100vh' };
 
   container?: HTMLElement | Window;
   activeLink?: string;
 
   private links: TDSAnchorLinkComponent[] = [];
   private animating = false;
   private destroy$ = new Subject();
   private handleScrollTimeoutID = -1;
 
   constructor(
     @Inject(DOCUMENT) private doc: TDSSafeAny,
     public tdsConfigService: TDSConfigService,
     private scrollSrv: TDSScrollService,
     private cdr: ChangeDetectorRef,
     private platform: Platform,
     private zone: NgZone,
     private renderer: Renderer2
   ) {}
 
   registerLink(link: TDSAnchorLinkComponent): void {
     this.links.push(link);
   }
 
   unregisterLink(link: TDSAnchorLinkComponent): void {
     this.links.splice(this.links.indexOf(link), 1);
   }
 
   private getContainer(): HTMLElement | Window {
     return this.container || window;
   }
 
   ngAfterViewInit(): void {
     this.registerScrollEvent();
   }
 
   ngOnDestroy(): void {
     clearTimeout(this.handleScrollTimeoutID);
     this.destroy$.next();
     this.destroy$.complete();
   }
 
   private registerScrollEvent(): void {
     if (!this.platform.isBrowser) {
       return;
     }
     this.destroy$.next();
     this.zone.runOutsideAngular(() => {
       fromEvent(this.getContainer(), 'scroll', <AddEventListenerOptions>passiveEventListenerOptions)
         .pipe(throttleTime(50), takeUntil(this.destroy$))
         .subscribe(() => this.handleScroll());
     });
     // Browser would maintain the scrolling position when refreshing.
     // So we have to delay calculation in avoid of getting a incorrect result.
     this.handleScrollTimeoutID = setTimeout(() => this.handleScroll());
   }
 
   handleScroll(): void {
     if (typeof document === 'undefined' || this.animating) {
       return;
     }
 
     const sections: Section[] = [];
     const offsetTop = this.tdsTargetOffset ? this.tdsTargetOffset : this.tdsOffsetTop || 0;
     const scope = offsetTop + this.tdsBounds;     
     this.links.forEach(comp => {
       const sharpLinkMatch = sharpMatcherRegx.exec(comp.tdsHref.toString());
       if (!sharpLinkMatch) {
         return;
       }
       const target = this.doc.getElementById(sharpLinkMatch[1]);
       if (target) {
         const top = getOffsetTop(target, this.getContainer());
         if (top < scope) {
           sections.push({
             top,
             comp
           });
         }
       }
     });
 
     this.visible = !!sections.length;
     if (!this.visible) {
       this.clearActive();
       this.cdr.detectChanges();
     } else {
       const maxSection = sections.reduce((prev, curr) => (curr.top > prev.top ? curr : prev));
       this.handleActive(maxSection.comp);
     }
     this.setVisible();
   }
 
   private clearActive(): void {
     this.links.forEach(i => {
       i.unsetActive();
     });
   }
 
   private setActive(comp?: TDSAnchorLinkComponent): void {

     const originalActiveLink = this.activeLink;
     const targetComp = (this.tdsCurrentAnchor && this.links.find(n => n.tdsHref === this.tdsCurrentAnchor)) || comp;
     if (!targetComp) return;
 
     targetComp.setActive();
     const linkNode = targetComp.getLinkTitleElement();
     this.ink.nativeElement.style.top = `${linkNode.offsetTop + linkNode.clientHeight / 2 - 4.5}px`;
     this.activeLink = (comp || targetComp).tdsHref;
     if (originalActiveLink !== this.activeLink) {
       this.tdsChange.emit(this.activeLink);
     }
   }
 
   private handleActive(comp: TDSAnchorLinkComponent): void {
     this.clearActive();
     this.setActive(comp);
     this.visible = true;
     this.setVisible();
     this.tdsScroll.emit(comp);
   }
 
   private setVisible(): void {
     const visible = this.visible;
     const visibleClassname = 'visible';
     if (this.ink) {
       if (visible) {
         this.renderer.addClass(this.ink.nativeElement, visibleClassname);
       } else {
         this.renderer.removeClass(this.ink.nativeElement, visibleClassname);
       }
     }
   }
 
   handleScrollTo(linkComp: TDSAnchorLinkComponent): void {
     const el = this.doc.querySelector(linkComp.tdsHref);
     if (!el) {
       return;
     }
 
     this.animating = true;
     const containerScrollTop = this.scrollSrv.getScroll(this.getContainer());
     const elOffsetTop = getOffsetTop(el, this.getContainer());
     let targetScrollTop = containerScrollTop + elOffsetTop;
     targetScrollTop -= this.tdsTargetOffset !== undefined ? this.tdsTargetOffset : this.tdsOffsetTop || 0;
     this.scrollSrv.scrollTo(this.getContainer(), targetScrollTop, {
       callback: () => {
         this.animating = false;
         this.handleActive(linkComp);
       }
     });
     this.tdsClick.emit(linkComp.tdsHref);
   }
 
   ngOnChanges(changes: SimpleChanges): void {
     const { tdsOffsetTop, tdsContainer, tdsCurrentAnchor } = changes;
     if (tdsOffsetTop) {
       this.wrapperStyle = {
         'max-height': `calc(100vh - ${this.tdsOffsetTop}px)`
       };
     }
     if (tdsContainer) {
       const container = this.tdsContainer;
       this.container = typeof container === 'string' ? this.doc.querySelector(container) : container;
       this.registerScrollEvent();
     }
     if (tdsCurrentAnchor) {
       this.setActive();
     }
   }
 }
 